<template>
    <div class="ma-5">
        <v-data-table :items="tags" :loading="loading" :page.sync="currentPage" :search="search"
                      :headers="headers" sort-by="id" class="elevation-2" hide-default-footer :items-per-page="10">
            <template v-slot:top>
                <v-toolbar flat>
                    <v-toolbar-title>标签列表</v-toolbar-title>
                    <v-divider class="mx-4" inset vertical></v-divider>
                    <v-spacer/>
                    <v-text-field v-model="search" append-icon="mdi-magnify" label="搜索" clearable single-line
                                  hide-details/>
                    <v-btn color="primary" :disabled="loading" icon class="mb-2 me-2" @click="refresh">
                        <v-progress-circular color="primary" size="18" width="2" v-if="loading" indeterminate/>
                        <v-icon v-else>mdi-refresh</v-icon>
                    </v-btn>
                    <v-btn color="primary" dark class="mb-2" replace :to="{path:'/admin/tag/newTag'}">新增</v-btn>
                </v-toolbar>
            </template>

            <template v-slot:item.id="{ item }">
                <v-icon small class="mr-2" @click="editItem(item)">mdi-pencil</v-icon>
                <strong>{{item.id}}</strong>
            </template>

            <template v-slot:item.name="{ item }">
                {{item.name}}
            </template>

            <template v-slot:item.createTime="{ item }">
                {{UTIL.formatTime(item.createTime)}}
            </template>

            <template v-slot:item.deleted="{ item }">
                <v-btn icon small @click="deleteItem(item)" color="error">
                    <v-icon>
                        {{item.deleted?"mdi-restore":"mdi-delete"}}
                    </v-icon>
                </v-btn>
            </template>
        </v-data-table>
        <v-pagination class="mt-5" v-model="currentPage" :length="totalPages"/>
    </div>
</template>

<script>

    export default {
        name: "TagList",
        components: {},
        data() {
            return {
                tags: [],
                currentPage: 1,
                totalPages: 0,
                loading: true,
                search: "",

                headers: [
                    {text: '序号', align: 'left', value: 'id'},
                    {text: '名称', value: 'name'},
                    {text: '创建时间', value: 'createTime'},
                    {text: '删除', value: 'deleted'},
                ],
            }
        },
        computed: {},
        watch: {
            currentPage(currentPage) {
                this.$vuetify.goTo(0)
                this.getTags(currentPage)
            }
        },
        methods: {
            editItem(item) {
                this.$router.push({
                    name: "/admin/tag/newTag",
                    params: {
                        tag: item
                    }
                })
            },
            deleteItem(item) {
                if (confirm("确定删除" + "：\n" + item.name)) {
                    let index = this.tags.indexOf(item)
                    let _this = this
                    this.axios.delete(this.API.URL.DELETE_TAG + item.id)
                        .then(function (result) {
                            let data = result.data
                            if (data.success) {
                                _this.tags.splice(index, 1)
                                alert("删除成功")
                            }
                        }, () => alert("删除失败"))
                }
            },
            refresh() {
                this.currentPage = 1
                this.getTags(this.currentPage)
            },
            getTags(currentPage) {
                this.loading = true
                let _this = this
                this.axios.get(this.API.URL.QUERY_TAG + (currentPage - 1))
                    .then(function (result) {
                        _this.loading = false
                        let data = result.data
                        if (data.success) {
                            _this.totalPages = data.data.totalPages
                            _this.tags = data.data.data
                            // console.log("_this.tags", _this.tags)
                        }
                    }, () => _this.loading = false)
            }
        },
        created() {
            this.getTags(1)
        }
    }
</script>

